import React, { Component, useRef } from 'react';

let bean;
let method_js;
let area;
let info_js;

const onChange1 = (event) => {
    bean = event.target.value;
}
const onChange2 = (event) => {
    method_js = event.target.value;
}
const onChange3 = (event) => {
    area = event.target.value;
}
const onChange4 = (event) => {
    info_js = event.target.value;
}
const onSubmit = (event) => {
    window.sessionStorage.setItem("bean", bean);
    window.sessionStorage.setItem("method", method_js);
    window.sessionStorage.setItem("area", area);
    window.sessionStorage.setItem("info", info_js);
};

class IndexMainComponent extends Component{

    render(){
        return(
            <main className="index-main">
                <form action="/cafe-list" method="get" className="search-box">
                    <input className="search-bean" type="text" list="beans-list" placeholder="  ?" name="bean" id="bean" onChange={onChange1}/>
                    <datalist id="beans-list">
                        <option value="AA" />
                        <option value="" />
                        <option value=" " />
                        <option value=" " />
                        <option value="" />
                        <option value=" " />
                    </datalist>

                    <div className="screen"></div>

                    <input className="search-method" type="text" list="extraction-method" placeholder=" " name="method" id="method_js" onChange={onChange2}/>
                    <datalist id="extraction-method">
                        <option value="" />
                        <option value=" " />
                        <option value=" " />
                        <option value="" />
                    </datalist>

                    <div className="screen"></div>

                    <input className="search-area" type="text" list="area" placeholder="" name="area" id="area" onChange={onChange3}/>
                    <datalist id="area">
                        <option value="" />
                        <option value="" />
                        <option value="" />
                        <option value="" />
                        <option value="" />
                        <option value="" />
                        <option value="" />
                    </datalist>

                    <div className="screen"></div>

                    <input className="search-cafe" type="text" list="cafe-info" placeholder=" " name="info" id="info" onChange={onChange4}/>
                    <datalist id="cafe-info">
                        <option value="" />
                        <option value="  " />
                        <option value="  " />
                    </datalist>

                    <button  type="submit" className="search-btn" onClick={onSubmit}>
                        <i className="fas fa-search fa-2x"></i>
                    </button>
                </form>
            </main>
        );
    }
}

export default IndexMainComponent;